import React, { Component } from 'react'
import BBIFrame from './BBIFrame'


class BBSparnatural extends BBIFrame {



  getUrl = ()  => `/${window.bbFolderName}/sparnatural/indexWistor.html`

  constructor() {
    super()
    //console.log("BBMap Constructor");
    this.scrolling = 'true'
    this.noEvents = false;
    this.usesParameters=false;
  }



}

export default BBSparnatural
